/* to be updated */
:root {
  --light-gray: #ababb1;
  --light-red: #fb5454;
  --light-blue: #6b7bb1;
  --light-green: #47ca47;

  --gray: #4d4d4d;
  --red: #ad1717;
  --blue: #262690;
  --green: #00641e;

  --white: #a3a3a3;
}

:root {
  --background: #111111;
}

.app {
  height: 100%;
  width: 100vw;
  position: absolute;
  top: 0;
  left: 0;

  background-color: var(--background);
}

.measuring-stick {
  position: absolute;
  height: 100%;
  top: 0;
  z-index: 1;
  left: 0;
  width: 4px;
  background-color: red;
}

:root {
  --top-padding: 5vh;
  --chat: 10vh;
  --game: 45vh;
  --pad: 35vh;
  --bottom-padding: 5vh;
}

@media (min-width: 1024px) {
  :root {
    --game: 80vh;
    --pad: 0vh;
  }
}

.top {
  height: var(--top-padding);
  background-color: pink;
}

.game {
  height: var(--game);
  background-color: green;
}

.chat {
  height: var(--chat);
  background-color: blue;
}

.pad {
  height: var(--pad);
  background-color: yellow;
}

.bottom {
  height: var(--bottom-padding);
  background-color: purple;
}
